import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { Items } from '../../../providers/index';
import {SocialSharing} from "@ionic-native/social-sharing";

@IonicPage()
@Component({
  selector: 'page-item-detail',
  templateUrl: 'item-detail.html'
})
export class ItemDetailPage {
  item: any;

  constructor(public navCtrl: NavController,
              private socialSharing: SocialSharing,

              navParams: NavParams, items: Items) {
    this.item = navParams.get('data') || items.defaultItem;
    console.log(this.item)
  }
  shareWithW(ms: any){
    this.socialSharing.shareViaWhatsApp(ms.title +"\n" + " By: " + ms.author + "\n"+ ms.message).catch(er=>{

    })

  }
    shareWithS(ms){
        this.socialSharing.shareViaWhatsApp(ms.title +"\n"+  " By: " + ms.author + "\n"+ ms.message).catch(er=>{


        });}
        shareWithF(ms){
      this.socialSharing.shareViaFacebook(ms.title +"\n" + " By:" + ms.author + "\n"+ ms.message).catch(er=>{


      })

    }

}



